export default function App() {
  const genreArr = Object.keys(seriesData);

  const [selectedGenre, setGenre] = useState("Anime");

  function buttonClickHandler(movieGenre) {
    setGenre(movieGenre);
  }

  return (
    <div className="App">
      <div className="container">
        <img src="https://img.icons8.com/external-flaticons-flat-flat-icons/64/000000/external-series-new-media-flaticons-flat-flat-icons.png" />
        <h1>CinePlex-Series Recommendation App</h1>
      </div>
      <h2>
        Checkout my list of brilliant series. Select a title to get started...
      </h2>

      <div>
        {genreArr.map((genre) => (
          <button
            className="btn"
            key={genre}
            onClick={() => buttonClickHandler(genre)}
          >
            {genre}
          </button>
        ))}
      </div>
      <hr />
      <div>
        <ul>
          {seriesData[selectedGenre].map((movie) => (
            <li className="list" key={movie.name}>
              <div className="movie">{movie.name}</div>
              <div className="desc">{movie.desc}</div>
              <div className="rating">Rating: {movie.rating}</div>
              <div className="star">Star: {movie.star}</div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
